.vetskitchen-posts-grid .grid-post {
  display: block;
  margin-bottom: 30px;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  background-color: #fafdff;
  border: 1px solid #e9e7e7;
  border-radius: 5px;
}
.vetskitchen-posts-grid .grid-post img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}
.vetskitchen-posts-grid .grid-post img.placeholder-img {
  background-color: #f2f2f2;
  object-fit: contain;
}
.vetskitchen-posts-grid .grid-post .details {
  padding: 20px;
}
.vetskitchen-posts-grid .grid-post .post-title {
  margin-bottom: 0;
  line-height: 1.2;
}
.vetskitchen-posts-grid .grid-post .date {
  margin-bottom: 0;
  font-size: 12px;
  color: #353535;
  opacity: 0.6;
}